/*reset*/
html { font-size: 16px; }
body { width: 100%; font-size: 1rem; line-height: 1.7; font-family: 'Roboto', 'Microsoft JhengHei', sans-serif; overflow-x: hidden; -webkit-text-size-adjust: none; -webkit-font-smoothing: antialiased; -ms-overflow-style: scrollbar; -moz-osx-font-smoothing: grayscale;}
html, body { margin: 0; }
a, a:visited, a:hover, a:focus, a:active { text-decoration: none; outline: none; }
*:focus, *:active { outline: 0; }
h1, h2, h3, h4, h5, h6 { font-weight: normal; line-height: 1.7; }
label { font-weight: inherit; }
input, button, textarea, select, optgroup, option { font-family: inherit; font-size: inherit; font-style: inherit; font-weight: inherit; outline: 0; }
textarea { resize: vertical; }
ol.reset, ul.reset { margin: 0; padding: 0; list-style: none; }
input, button, textarea, select { *font-size: 100%; }
code, kbd, samp, tt { font-size: 100%; }
*, *:after, *::before { -webkit-box-sizing: border-box; box-sizing: border-box; }

