.letter{display: inline-block; vertical-align: middle;}
.container{ position: relative;  }
.main{  height: 100vh; overflow: hidden; display: grid; grid-template-columns: 30% 70%; grid-template-rows: 100%; position: relative; z-index: 1; align-items: stretch;}
.main .picture{justify-self: center;}
.main .picture img{height: 100%;}
.main .content{ position: relative; display: grid; grid-template-columns: 100%; grid-template-rows: 50% 50%; }
.main .content:before{content: ""; position: absolute; width: 125%; height: 100%; right: 0; top: 0; background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 20%,rgba(238,252,255,1) 100%);}
.main .content .email{position: fixed; top: 20px; right: 20px; width: 26px; height: 26px; z-index: 1;}
.main .content .email img{width: 100%;}
.main .content .header{margin-bottom: 80px; position: relative; display: grid;  grid-template-columns: 50% 50%; justify-content: space-between; align-items: center; }
.main .content .header .logo{ position: relative; perspective: 500px;}
.main .content .header .logo p{margin: 0; font-size: 1.812rem; font-weight: bold; margin-left: 20px; display: inline-block; }
.main .content .header .logo h1{ margin: 0; line-height: 1; font-size: 6.25rem; font-weight: bold; color: #000;}
.main .content .header .logo svg{position: absolute; z-index: -1; opacity: 0.5; width: 470px; height: 400px; top: -80px; left: -180px;}
.main .content .header .logo svg #shape1{transform-origin: right center;}
.main .content .header .logo svg #shape2{transform-origin: left center;}
.main .content .header .menu{ justify-self: right; margin-top: 100px; position: relative; z-index: 2;}
.main .content .header .menu ul{ position: relative; z-index: 2;}
.main .content .header .menu li{display: inline-block;}
.main .content .header .menu li:before{content: ""; width: 50px; height: 1px; display: inline-block; vertical-align: middle; background: #000; margin: 0 10px 5px;}
.main .content .header .menu li button{background: none; border: none; font-size: 1.25rem; font-weight: bold; cursor: pointer;}
.main .content .header .menu li:last-child:after{content: ""; width: 50px; height: 1px; display: inline-block; vertical-align: middle; background: #000; margin: 0 0px 5px 10px;}
.main .content .carousel{ width: 100%; overflow: hidden; justify-self: end; align-self: end; padding: 80px 0; z-index: 1; position: relative;}
.main .content .carousel .project-list{ position: relative; z-index: 2;}
.main .content .carousel .project-list .item{padding-left: 80px; }
.main .content .carousel .project-list .item img{height: 455px; max-height: 45vh; display: block;}
.main .content .carousel .project-list .item a{display: block; transition: transform 0.5s, box-shadow 0.5s;}
.main .content .carousel .project-list .item a:hover{transform: scale(1.05); box-shadow: 20px 20px 10px rgba(0, 0, 0, 0.25)}
.main .content .carousel .parallaxBG{position: absolute; left: 0; top: 0; width: 100%; height: 100%; z-index: 1;}
.main .content .carousel .parallaxBG i{position: absolute; display: block; transform: skewX(-40deg); transition: left 1s linear;}
.main .content .carousel .parallaxBG .rect1{
  width: 145px; 
  height: 30px; 
  background: linear-gradient(to right, rgba(174,207,215,1) 0%,rgba(240,249,255,1) 100%);
  top: 0px;
}
.main .content .carousel .parallaxBG .rect2{
  width: 220px; 
  height: 8px; 
  background: #ffe9bc;
  top: 50px;
}
.main .content .carousel .parallaxBG .rect3{
  width: 145px; 
  height: 30px; 
  background: linear-gradient(to right, rgba(174,207,215,1) 0%,rgba(240,249,255,1) 100%);
  bottom: 30px;
  z-index: 2;
}
.main .content .carousel .parallaxBG .rect4{
  width: 220px; 
  height: 8px; 
  background: #ffe9bc;
  bottom: 40px;
}
.main .content .carousel .parallaxBG .circle{
  width: 60px; 
  height: 60px; 
  transform: none; 
  border-radius: 50%;
  background: linear-gradient(135deg, #edbcee 0%, #ff51bb 100%);
  bottom: 50px;
}
@media (max-width:1440px){
  .main .content .header{margin-bottom: 5px;}
  .main .content .header .logo p{font-size: 1.65rem;}
  .main .content .header .logo h1{ font-size: 5rem;}
}
@media (max-width:1200px){
  .main .content .header .logo svg{width: 350px; left: -100px; top: -130px;}
  .main .content .header .menu li:before, .main .content .header .menu li:last-child:after{width: 30px;}
}
@media (max-width:990px){
  .main{ height: auto; display: block; position: relative;}
  .main .picture{height: 300px; width: 100%; top: 0; left: 0; position: absolute;}
  .main .picture img{position: absolute; height: auto; width: 100%; top: 50%; left: 0; transform: translateY(-35%);}
  .main .content{margin-top: 200px; padding: 50px 50px 0; display: block;}
  .main .content:before{width: 100%; height: 120%; top: -20px; background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 20%,rgba(238,252,255,1) 100%);}
  .main .content .header .menu li:last-child:after{display: none;}
}
@media (max-width:640px){
  .main .content{padding: 20px 20px 0;}
  .main .content .header{display: block;}
  .main .content .header .logo p{font-size: 1.25rem;}
  .main .content .header .logo h1{ font-size: 4rem;}
  .main .content .header .menu{margin-top: 0px; padding: 20px;  display: inline-block; position: fixed; top: 0; left: 0;}
  .main .content .header .menu li:before{display: none;}
  .main .content .header .menu li button{font-size: 1rem; margin-right: 20px;}
  .main .content .carousel .project-list .item{padding: 0 10px; width: 100%;}
  .main .content .carousel .project-list .item img{height: 100%; max-height: 100%; width: 100%; }
}  
.popWrap{position: fixed; top: 0; left: 0; width: 100%; min-height: 100%; z-index: 99999; background: rgba(255,255,255,0.95); opacity: 0; transform: scale(0); transition: opacity .5s, transform 0s .5s;}
.popWrap.fadeIn{opacity: 1; transform: scale(1); transition: opacity .5s, transfrom 0s;}
.popWrap .close{ position: absolute; right: 20px; top: 20px; z-index: 999; width: 70px; height: 70px; border: 1px solid #000; background: none; cursor: pointer; font-size: 0;}
.popWrap .close:before, .popWrap .close:after{content: ""; width: 80%; height: 1px; background: #000; display: block; position: absolute; left: 10%; top: 50%;}
.popWrap .close:before{transform: rotate(45deg);}
.popWrap .close:after{transform: rotate(-45deg);}
/*Skill*/
@keyframes txtani{
  0%{
    left: 0;
  }
  100%{
    left: -1840px;
  }
}
@media (max-width:1440px){
  @keyframes txtani{
    0%{
      left: 0;
    }
    100%{
      left: -1308px;
    }
  }
}
@media (max-width:640px){
  @keyframes txtani{
    0%{
      left: 0;
    }
    100%{
      left: -865px;
    }
  }
}

.skill{overflow: hidden;}
.skill .title {
  white-space: nowrap;
  font-size: 0;
  display: block;
  position: relative;
  animation: txtani 13s 0s linear infinite;
}
.skill .title span{
  display: inline-block;
  font-size: 8.75rem;
  color: #aecfd7;
  letter-spacing: 10px;
  text-transform: uppercase;
  font-weight: bold;
}
.skill .title span:before{
  content: "";
  width: 105px;
  height: 120px;
  display: inline-block;
  margin: 0 80px -10px 0;
  background: url(./images/triangle.png);
}
.skill .title span:after{
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 1000px;
  height: 1px;
  margin: 0 80px 5px;
  background: #e5e5e5;
}
.skill .details{
  padding: 10px 50px 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;  
}
.skill .details .box{
  flex: auto auto;
  padding: 0 15px;
}
.skill .details .s-logos{
  margin-bottom: 10px;
}
.skill .details .s-logos img{margin-right: 5px;}
.skill .details .box p{
  font-size: 1.875rem;
  margin: 0 0 5px;
} 
.skill .bg{overflow: hidden; height: 400px; position: relative; margin: 0 50px 50px;}
.skill .bg img{position: absolute; left: 50%; top: 50%; min-width: 100%;
  transform: translate(-50%, -50%);
}
@media (max-width:1440px){
  .skill .title{padding: 20px 0 10px;}
  .skill .title span{ font-size: 6.25rem; letter-spacing: 5px;}
  .skill .title span:before{
    margin: 0 50px -20px 0;
  }
  .skill .title span:after{
    width: 700px;
    margin: 0 50px 5px;
  }
  .skill .details .box p{
    font-size: 1.25rem;
  } 
}
@media (max-width:990px){
  .skill .details{ padding: 10px 30px 40px;}
  .skill .details .s-logos img{max-width: calc(20% - 5px);}
  .skill .details .box{ flex: 1;}
  .skill .bg{margin: 0 30px 50px;}
}
@media (max-width:640px){
  .skill .title span{ font-size: 4rem;}
  .skill .title span:before{ margin: 0 50px -20px 0; width: 80px; height: 90px; background-size: 100%; }
  .skill .title span:after{ width: 400px; }
  .skill .details{flex-wrap: wrap; padding: 10px 30px 0px;}
  .skill .details .box{ flex: auto; width: 50%; position: relative; padding-left: 50px; margin-bottom: 30px;}
  .skill .details .box p{display: block; width: 100%;} 
  .skill .details .s-logos {width: 35px; position: absolute; left: 0; top: 0;}
  .skill .details .s-logos img{max-width: 100%; display: block; margin-bottom: 5px;}
  .skill .bg{margin: 0 30px 50px; height: 250px;}
  .skill .bg img{height: 100%;}
}
@media (max-width:370px){
  .skill .details .box{ width: 100%;}
}
  /*experience*/
.experience{
  width: 100vw;
  height: 100vh;
  display: grid; 
  grid-template-columns: 70% 30%; 
  grid-template-rows: 100%;
}
.experience .detail{
  padding: 50px;
}
.experience .detail .title{
  font-size: 8.75rem;
  color: #aecfd7;
  font-weight: bold;
  margin: 0;
}
.experience .detail .text{
  font-size: 1.625rem;
}
.experience .detail .ex{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 100px;
}
.experience .detail .ex .box{
  flex: auto; font-size: 1.5rem; color: #292929;
}
.experience .detail .ex .box .jobTitle{
  font-size: 2.5rem; font-weight: 300; margin: 0; color: #000;
}
.experience .bg{
  position: relative;
  background: url(./images/experience-pic.jpg);
  background-size: cover;
}
.experience .bg img{
  position: absolute;
  bottom: 10%;
  left: -25%;
  max-width: 100%;
}
@media (max-width:1440px){
  .experience .detail .title {
    font-size: 6.25rem;
  }
  .experience .detail .text{
    font-size: 1.25rem;
  }
  .experience .detail .ex{
    margin-top: 60px;
  }
  .experience .detail .ex .box{
    font-size: 1.25rem;
  }
  .experience .detail .ex .box .jobTitle{
    font-size: 2rem; 
  }
}
@media (max-width:990px){
  .experience .detail .title {
    font-size: 4rem;
  }
  .experience .detail .text{
    font-size: 1.125rem;
  }
  .experience .detail .ex{
    display: block;
    margin-top: 40px;
  }
  .experience .detail .ex .box{
    font-size: 1.125rem; margin-bottom: 30px;
  }
  .experience .detail .ex .box .jobTitle{
    font-size: 1.875rem; 
  }
  .experience .detail .ex .box p{margin: 0;}
}
@media (max-width:640px){
  .experience{position: relative; display: block;}
  .experience .bg{display: none; position: absolute; right: 0; top: 0; height: 100%; width: 40%;}
  .experience .bg img{ bottom: 5%;}
  .experience .detail{
    padding: 30px;
    position: relative;
    z-index: 2;
  }
  .experience .detail .title {
    font-size: 3rem;
  }
  .experience .detail .text{
    font-size: 1.06rem;
  }
  .experience .detail .ex .box{
    font-size: 1.06rem;
  }
  .experience .detail .ex .box .jobTitle{
    font-size: 1.625rem; 
  }
}
